}
.jc-table-small > .jc-table-content .jc-table-placeholder,
.jc-table-small > .jc-table-content .jc-table-row:last-child td {
  border-bottom: 0;
}
.jc-table-small.jc-table-bordered {
  border-right: 0;
}
.jc-table-small.jc-table-bordered .jc-table-title {
  border: 0;
  border-bottom: 1px solid #e8e8e8;
  border-right: 1px solid #e8e8e8;
}
.jc-table-small.jc-table-bordered .jc-table-content {
  border-right: 1px solid #e8e8e8;
}
.jc-table-small.jc-table-bordered .jc-table-footer {
  border: 0;
  border-top: 1px solid #e8e8e8;
  border-right: 1px solid #e8e8e8;
}
.jc-table-small.jc-table-bordered .jc-table-footer:before {
  display: none;
}
.jc-table-small.jc-table-bordered .jc-table-placeholder {
  border-left: 0;
  border-bottom: 0;
}
.jc-table-small.jc-table-bordered .jc-table-thead > tr > th:last-child,
.jc-table-small.jc-table-bordered .jc-table-tbody > tr > td:last-child {
  border-right: none;
}
